import store from '@/store';
import { Module, VuexModule, Mutation, Action, getModule } from 'vuex-module-decorators';
import AppreciationCard from '@/data/ac';
import { APPRECIATION_CARD_STATE, AppreciationCardData, MinimizedAppreciationCard, TOKEN } from '@/interfaces/iac';
import AppreciationCardIDB from '@/databases/acidb';
import tokensJSON from '@/store/misc/tokens.json';

const acidb = new AppreciationCardIDB('ac3', 1);

// acidb.addAC({
//   sender: 'carlos@carlos.com',
//   receiver: 'gerard.van_dijk@airbus.com',
//   message: 'Short message',
//   dateCreated: 1591778542123,
//   dateSent: 1591778542456,
//   state: 2,
//   topic: 5,
//   subtopics: [1,2],
//   token: 1,
//   bonus: 4,
//   streak: 8
// });

const WEEK: number = (1000 * 60 * 60 * 24 * 7);

export interface TokenConfig {
  MAX_AWESOME_WEEK_TOTAL: number;
  MAX_AWESOME_WEEK_PERSON: number;
  MAX_REWARD_WEEK_TOTAL: number;
  MAX_REWARD_WEEK_PERSON: number;
  AWESOME_TOKENS_PER_CARD: number;
  REWARD_TOKENS_PER_CARD: number;
  REWARD_BADGE_TOKENS_PER_LEVEL: number;
  REWARD_BADGE_LEVELS: number;
  AWESOME_BADGE_TOKENS_PER_LEVEL: number;
  AWESOME_BADGE_LEVELS: number;
  LOGIN_BADGE_LEVELS: number;
  STREAK_BADGE_LEVELS: number;
  STREAK_RESET_DAYS: number;
}

export interface ACState {
  acs: Array<AppreciationCard>;
  config: TokenConfig;
}

export interface Badge {
  level: number;
  levelMax: number;
  current: number;
  levelUp: number;
  color: string;
}

@Module({ dynamic: true, store, name: 'acModule'})
export default class ACModule extends VuexModule implements ACState {
  public acs: Array<AppreciationCard> = [];
  public config: TokenConfig = tokensJSON.config;

  @Mutation
  updateACs(acds: AppreciationCardData[]): void {
    this.acs = [];
    acds.forEach((acd: AppreciationCardData) => this.acs.push(new AppreciationCard(acd)));
  }

  @Mutation
  addLocalAC(ac: AppreciationCard): void {
    this.acs = [...this.acs, ac];
  }

  @Mutation
  updateLocalAC(newAC: AppreciationCard): void {
    const localIndex = this.acs.findIndex((ac: AppreciationCard) => ac.id === newAC.id);
    this.acs[localIndex] = newAC;
    this.acs = [...this.acs];
  }
  
  @Mutation
  deleteLocalAC(id: number): void {
    this.acs = this.acs.filter(ac => ac.id != id);
  }
  
  @Mutation
  deleteLocalACs(): void {
    this.acs = [];
  }

  @Action({commit: 'updateACs'})
  async fetchACs(): Promise<AppreciationCardData[]> {
    const acs: AppreciationCardData[] = await acidb.getACs();
    return acs;
  }
  
  @Action({commit: 'addLocalAC'})
  async addAC(acd: AppreciationCardData): Promise<AppreciationCard>{
    const id = await acidb.addAC(acd);
    acd.id = id;
    return new AppreciationCard(acd);
  }
  
  @Action({commit: 'updateLocalAC'})
  async updateAC(acd: AppreciationCardData): Promise<AppreciationCard> {
    await acidb.updateAC(acd);
    return new AppreciationCard(acd);
  }
  
  @Action({commit: 'updateLocalAC'})
  async archiveAC(ac: AppreciationCard): Promise<AppreciationCard> {
    const acd = ac.toACData();
    acd.state = APPRECIATION_CARD_STATE.ARCHIVED;
    await acidb.updateAC(acd);
    return new AppreciationCard(acd);
  }
  
  @Action({commit: 'deleteLocalAC'})
  async deleteAC(id: number): Promise<number> {
    const result = await acidb.deleteAC(id);
    if (result) return id;
    return -1;
  }
  
  @Action({commit: 'deleteLocalACs'})
  async deleteAllACs(): Promise<boolean> {
    const result = await acidb.deleteAllACs();
    return result;
  }
  
  @Mutation
  refreshState(): void {
    this.acs = [...this.acs];
  }
  
  @Action({commit: 'refreshState'})
  async loginBonusUpdated(): Promise<void> {
    return;
  }
  
  get exportACs(): Promise<AppreciationCardData[]> {
    return acidb.getACs();
  }

  get allACs(): Array<AppreciationCard> {
    return this.acs.filter(ac => ac.state !== APPRECIATION_CARD_STATE.ARCHIVED);
  }

  get pendingACs(): Array<AppreciationCard> {
    return this.acs.filter(ac => ac.state === APPRECIATION_CARD_STATE.PENDING);
  }

  get receivedACs(): Array<AppreciationCard> {
    return this.acs.filter(ac => ac.state === APPRECIATION_CARD_STATE.RECEIVED);
  }

  get sentACs(): Array<AppreciationCard> {
    return this.acs.filter(ac => ac.state === APPRECIATION_CARD_STATE.SENT);
  }

  get canStreak(): number {
    const DAY = (1000 * 60 * 60 * 24);
    const WEEK = (1000 * 60 * 60 * 24 * 7);
    const now = Date.now();
    if (this.acs.filter(ac => new Date(ac.dateSent).getDay() === new Date(now).getDay() && ac.dateSent > now - DAY).length > 0)
      return -1;
    const lastWeekACs = this.acs.filter(ac => ac.dateSent > now - WEEK && ac.streak > 0);
    if (lastWeekACs.length > 0 )
      return Math.max(...lastWeekACs.map(ac => ac.streak));
    return 0;
  }

  get currentStreak(): number {
    const WEEK = (1000 * 60 * 60 * 24 * 7);
    const lastWeekACs = this.acs.filter(ac => ac.dateSent > Date.now() - WEEK && ac.streak > 0);
    if (lastWeekACs.length > 0 )
      return Math.max(...lastWeekACs.map(ac => ac.streak));
    return 0;
  }

  get acById() {
    return (id: number): AppreciationCard => 
      this.acs.find(ac => ac.id === id)!;
  }

  get acByMinimizedAC() {
    return (mac: MinimizedAppreciationCard) => 
      this.acs.find(ac => ac.equals(mac));
  }

  get rewardTokenCount(): number {
    const rewardACs = this.acs.filter(ac => ac.token === TOKEN.REWARD);
    return rewardACs.length * this.config.REWARD_TOKENS_PER_CARD + rewardACs.map(ac => ac.bonus).reduce((a, b) => a + b, 0);
  }

  get awesomeTokenCount(): number {
    const awesomeACs = this.acs.filter(ac => ac.token === TOKEN.AWESOME);
    const localBonus = localStorage.getItem('bonus');
    const bonusCount: number = localBonus ? JSON.parse(localBonus).points : 0
    return awesomeACs.length * this.config.AWESOME_TOKENS_PER_CARD + awesomeACs.map(ac => ac.bonus).reduce((a, b) => a + b, 0) + bonusCount;
  }

  get rewardBadge(): Badge {
    const count = this.rewardTokenCount;
    const level = Math.floor(count / this.config.REWARD_BADGE_TOKENS_PER_LEVEL);
    return {
      level: level > this.config.REWARD_BADGE_LEVELS ? this.config.REWARD_BADGE_LEVELS : level,
      levelMax: this.config.REWARD_BADGE_LEVELS,
      current: count % this.config.REWARD_BADGE_TOKENS_PER_LEVEL,
      levelUp: this.config.REWARD_BADGE_TOKENS_PER_LEVEL,
      // levelUp: this.config.REWARD_BADGE_TOKENS_PER_LEVEL - (count % this.config.REWARD_BADGE_TOKENS_PER_LEVEL)
      color: 'blue darken-2'
    }
  }

  get awesomeBadge(): Badge {
    const count = this.awesomeTokenCount;
    const level = Math.floor(count / this.config.AWESOME_BADGE_TOKENS_PER_LEVEL);
    return {
      level: level > this.config.AWESOME_BADGE_LEVELS ? this.config.AWESOME_BADGE_LEVELS : level,
      levelMax: this.config.AWESOME_BADGE_LEVELS,
      current: count % this.config.AWESOME_BADGE_TOKENS_PER_LEVEL,
      levelUp: this.config.AWESOME_BADGE_TOKENS_PER_LEVEL,
      // levelUp: this.config.AWESOME_BADGE_TOKENS_PER_LEVEL - (count % this.config.AWESOME_BADGE_TOKENS_PER_LEVEL)
      color: 'red darken-2'
    }
  }

  get streakBadge(): Badge {
    let count = Math.max(...this.acs.map(ac => ac.streak));
    if (count < 0) count = 0;
    if (count > this.config.STREAK_BADGE_LEVELS) count = this.config.STREAK_BADGE_LEVELS
    return {
      level: count,
      levelMax: this.config.STREAK_BADGE_LEVELS,
      current: count,
      levelUp: 1,
      color: 'orange darken-2'
    }
  }

  get loginBadge(): Badge {
    const localBonus = localStorage.getItem('bonus');
    const count: number = localBonus ? JSON.parse(localBonus).maxStreak : 1;
    return {
      level: count > this.config.LOGIN_BADGE_LEVELS ? this.config.LOGIN_BADGE_LEVELS : count,
      levelMax: this.config.LOGIN_BADGE_LEVELS,
      current: count,
      levelUp: 1,
      color: 'pink darken-2'
    }
  }

  get tokenConfig(): TokenConfig {
    return this.config;
  }

  get canObtainRewardToken() {
    return (acd: AppreciationCardData) => {
      const list: Array<AppreciationCard> = this.acs.filter(ac => 
        ac.dateSent > Date.now() - WEEK
        && ac.state === APPRECIATION_CARD_STATE.SENT);

      if (this.config.MAX_REWARD_WEEK_TOTAL > -1)
        if (list.length >= this.config.MAX_REWARD_WEEK_TOTAL)
          return false;

      if (this.config.MAX_REWARD_WEEK_PERSON > -1)
        if (list.filter(ac => 
          ac.dateSent > Date.now() - WEEK 
          && ac.receiver.email === acd.receiver
          && ac.state === APPRECIATION_CARD_STATE.SENT
        ).length >= this.config.MAX_REWARD_WEEK_PERSON) 
          return false;

      return true;
    }
  }

  get canObtainAwesomeToken() {
    return (acd: AppreciationCardData) => {
      const list: Array<AppreciationCard> = this.acs.filter(ac => 
        ac.dateSent > Date.now() - WEEK
        && ac.state === APPRECIATION_CARD_STATE.RECEIVED);

      if (this.config.MAX_AWESOME_WEEK_TOTAL > -1)
        if (list.length >= this.config.MAX_AWESOME_WEEK_TOTAL)
          return false;

      if (this.config.MAX_AWESOME_WEEK_PERSON > -1)
        if (list.filter(ac => 
          ac.dateSent > Date.now() - WEEK 
          && ac.receiver.email === acd.receiver
          && ac.state === APPRECIATION_CARD_STATE.RECEIVED
        ).length >= this.config.MAX_AWESOME_WEEK_PERSON) 
          return false;

      return true;
    }
  }
}

export const acModule = getModule(ACModule) ;