import store from '@/store';
import { Module, VuexModule, Mutation, Action, getModule } from 'vuex-module-decorators';

export enum SERVICE_WORKER_STATUS {
  INACTIVE = 0,
  REGISTERED = 2,
  ACTIVE = 1,
  UPDATING = 2,
  UPDATED = 3,
}

export interface ServiceWorkerState {
  status: SERVICE_WORKER_STATUS;
}

@Module({ dynamic: true, store, name: 'swModule'})
export default class ServiceWorkerModule extends VuexModule implements ServiceWorkerState {
  public status: SERVICE_WORKER_STATUS = SERVICE_WORKER_STATUS.INACTIVE;

  @Mutation
  updateSWStatus(status: SERVICE_WORKER_STATUS): void {
    this.status = status;
  }

  @Action({commit: 'updateSWStatus'})
  async setSWState(status: SERVICE_WORKER_STATUS): Promise<SERVICE_WORKER_STATUS> {
    return status;
  }
  
  get swState(): SERVICE_WORKER_STATUS {
    return this.status;
  }
}

export const serviceWorkerModule = getModule(ServiceWorkerModule);