









































































import Vue from 'vue';
import ServiceWorkerWatcher from '@/components/ServiceWorkerWatcher.vue';
import LoginBonus from '@/components/LoginBonus.vue';

import { acModule } from '@/store/modules/acs';
import iconConfig from '@/store/misc/icons';
import {version} from '../package.json';

export default Vue.extend({
  name: 'App',
  components: {
    ServiceWorkerWatcher,
    LoginBonus
  },
  data() {
    return {
      iconConfig,
      version
    }
  },
  computed: {
    currentPageTitle() {
      return this.$route.name;
    },
    showBackButton() {
      if (this.$route.path === '/') return false;
      if (this.$route.name === 'Setup') return false;
      if (this.$route.name === 'Install') return false;
      return true;
    },
    showAppBar() {
      if (this.$route.name === 'Install') return false;
      return true;
    },
    rewardTokenCount(): number {
      return acModule.rewardTokenCount;
    },
    awesomeTokenCount(): number {
      return acModule.awesomeTokenCount;
    }
  },
  // created: function() {
  //   topicModule.fetchTopics()
  //     .then(() => imageModule.fetchImages())
  //     .then(() => peopleModule.fetchPeople())
  //     .then(() => acModule.fetchACs())
  //     .then(() => convertToIDB())
  // }
});
