


































import Vue from 'vue';
import { serviceWorkerModule, SERVICE_WORKER_STATUS } from '@/store/modules/sw';

export default Vue.extend({
  name: 'ServiceWorkerWatcher',
  data() {
    return {
      updating: false,
      updated: false
    }
  },
  methods: {
    reloadApp(): void {
      this.updated = false;
      window.location.reload();
    },
    processStatus(status: SERVICE_WORKER_STATUS): void {
      switch (status) {
        case SERVICE_WORKER_STATUS.INACTIVE: break;
        case SERVICE_WORKER_STATUS.ACTIVE: break;
        case SERVICE_WORKER_STATUS.UPDATING: 
          this.updating = true;
          break;
        case SERVICE_WORKER_STATUS.UPDATED: 
          this.updated = true;
          break;
        default: break;
      }
    }
  },
  computed: {
    state() {
      return serviceWorkerModule.swState;
    }
  },
  watch: {
    state: function(newStatus: SERVICE_WORKER_STATUS) {
      this.processStatus(newStatus);
    }
  },
  mounted() {
    this.processStatus(this.state);
  }
});
