import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import { peopleModule } from '@/store/modules/people';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Appreciation App',
    component: Home
  },
  {
    path: '/send',
    name: 'Send',
    component: () => import('../views/Send.vue')
  },
  {
    path: '/create',
    name: 'Create',
    component: () => import('../views/Create.vue')
  },
  {
    path: '/receive',
    name: 'Receive',
    component: () => import('../views/Receive.vue')
  },
  {
    path: '/tokens',
    name: 'Tokens',
    component: () => import('../views/Tokens.vue')
  },
  {
    path: '/setup',
    name: 'Setup',
    component: () => import('../views/Setup.vue')
  },
  {
    path: '/received',
    name: 'Received',
    component: () => import('../views/Received.vue')
  },
  {
    path: '/sent',
    name: 'Sent',
    component: () => import('../views/Sent.vue')
  },
  {
    path: '/contacts',
    name: 'Contacts',
    component: () => import('../views/Contacts.vue')
  },
  {
    path: '/settings',
    name: 'Settings',
    component: () => import('../views/Settings.vue')
  },
  {
    path: '/install',
    name: 'Install',
    component: () => import('../views/Install.vue')
  },
  {
    path: '/faq',
    name: 'FAQ',
    component: () => import('../views/FAQ.vue')
  },
  {
    path: '/guide',
    name: 'Guide',
    component: () => import('../views/Guide.vue')
  },
  {
    path: '/permissions',
    name: 'Permissions',
    component: () => import('../views/Permissions.vue')
  },
  {
    path: '/report',
    name: 'Report',
    component: () => import('../views/Report.vue')
  },
  {
    path: '/share',
    name: 'Share',
    component: () => import('../views/Share.vue')
  }
];

const router = new VueRouter({
  routes
});

router.beforeEach((to, from, next) => {
  if (matchMedia('(display-mode: standalone)').matches || window.localStorage.getItem('noInstall')) {
    if (to.name !== 'Setup' && !peopleModule.selfPerson) next({ name: 'Setup' });
    else if (to.name === 'Setup' && peopleModule.selfPerson) next({ path: '/' });
    else if (to.name === 'Install') next({ path: '/' });
    else next();
  } else {
    if (to.name !== 'Install') next({ name: 'Install' });
    else next();
  }
});

export default router;