import { MinimizedPerson } from './ipeople';
import { peopleModule } from '@/store/modules/people';

export interface AppreciationCardData {
  id? : number;
  sender: string;
  receiver: string;
  message: string;
  dateCreated: number;
  dateSent: number;
  state: number;
  topic: number | string;
  subtopics: Array < number | string > ;
  token?: number;
  bonus?: number;
  streak?: number;
}

export interface AppreciationTopic {
  id: number;
  text: string;
  deleted: boolean;
  color: string;
  icon: string;
  subtopics: Array < AppreciationSubTopic > ;
}

export interface AppreciationSubTopic {
  id: number;
  text: string;
  deleted: boolean;
}

export enum APPRECIATION_CARD_STATE {
    NEWCONTACT = 0,
    PENDING = 1,
    SENT = 2,
    RECEIVED = 3,
    ARCHIVED = 4,
    BONUS = 5
}

export interface MinimizedAppreciationCard {
  e: string;
  m: string;
  d: number;
  t: number | string;
  s: Array < number | string > ;
  p? : MinimizedPerson;
}

export enum TOKEN {
  NONE = 0,
  REWARD = 1,
  AWESOME = 2
}

export function minimizedToACData(mac: MinimizedAppreciationCard): AppreciationCardData {
  const acd: AppreciationCardData = {
    sender: mac.e,
    receiver: peopleModule.selfPerson ? peopleModule.selfPerson.email : '',
    message: mac.m,
    dateCreated: mac.d,
    dateSent: Date.now(),
    state: APPRECIATION_CARD_STATE.RECEIVED,
    topic: mac.t,
    subtopics: mac.s
  }
  return acd;
}

export interface ConfirmationMessage {
  e: string;
  d: number;
  n: number;
  p? : MinimizedPerson;
}

export function calculateBonus(streak: number): number {
  return Math.floor(Math.sqrt(streak * 3));
}

export function calculateNextBonus(streak: number): number {
  const bonus = calculateBonus(streak);
  const nextBonus = (bonus + 1) * (bonus + 1) / 3;
  return nextBonus - streak;
}
