import { PersonData } from '@/interfaces/ipeople';

class PeopleIDB {

  private db?: IDBDatabase;
  protected name: string;
  protected version: number;
  private OBJECT_STORE_NAME = 'people';

  constructor(name: string, version: number) {
    this.checkCompatibility();
    this.name = name;
    this.version = version;
  }

  private checkCompatibility() {
    if (!window.indexedDB) console.log("Your browser doesn't support a stable version of IndexedDB. Such and such feature will not be available.");
  }

  async getDB(objectStoreName: string): Promise<IDBDatabase> {
    return new Promise((resolve, reject) => {
      if (this.db) return resolve(this.db);
      const request: IDBOpenDBRequest = window.indexedDB.open(this.name, this.version);
      request.onerror = (e: any) => {
        console.error("Error opening database: ");
        console.error(e);
        reject(e);
      }
      request.onsuccess  = (e: any) => {
        this.db = e.target.result as IDBDatabase;
        this.db.onerror = (e: any) => console.error("Database error: " + e.target.errorCode);
        resolve(this.db);
      }
      request.onupgradeneeded = (e: any) => {
        const db = e.target.result as IDBDatabase;
        const objectStore = db.createObjectStore(objectStoreName, { keyPath: "email" });
        objectStore.createIndex("name", "name", {unique: false});
      }
    })
  }
  
  async getPeople(): Promise<PersonData[]> {
    const db: IDBDatabase = await this.getDB(this.OBJECT_STORE_NAME);
    return new Promise((resolve) => {
      const people: PersonData[] = [];
      const transaction = db.transaction([this.OBJECT_STORE_NAME], 'readonly');
      transaction.oncomplete = () => resolve(people);
      //TODO: objectstore.getAll() possible without transaction? Is a transaction necessary?
      transaction.objectStore(this.OBJECT_STORE_NAME).openCursor().onsuccess = (e: any) => {
        const cursor: IDBCursorWithValue = e.target.result || null;
        if (cursor) {
          const person: PersonData = cursor.value;
          people.push(person);
          cursor.continue();
        }
      }
    });
  }
  
  async addPerson(person: PersonData): Promise<string> {
    const db: IDBDatabase = await this.getDB(this.OBJECT_STORE_NAME);
    return new Promise((resolve) => {
      const transaction = db.transaction([this.OBJECT_STORE_NAME], 'readwrite');
      let key: IDBValidKey; 
      transaction.oncomplete = (e) => resolve(key as string);
      const request: IDBRequest<IDBValidKey> = transaction.objectStore(this.OBJECT_STORE_NAME).put(person);
      request.onsuccess = (e: any) => key = e.target.result;
    });
  }
  
  async deletePerson(id: number): Promise<boolean> {
    const db: IDBDatabase = await this.getDB(this.OBJECT_STORE_NAME);
    return new Promise((resolve) => {
      const transaction = db.transaction([this.OBJECT_STORE_NAME], 'readwrite');
      transaction.oncomplete = () => resolve(true);
      transaction.objectStore(this.OBJECT_STORE_NAME).delete(id);
    });
  }
  
  async deleteAllPeople(): Promise<boolean> {
    const db: IDBDatabase = await this.getDB(this.OBJECT_STORE_NAME);
    return new Promise((resolve) => {
      const transaction = db.transaction([this.OBJECT_STORE_NAME], 'readwrite');
      transaction.oncomplete = () => resolve(true);
      transaction.objectStore(this.OBJECT_STORE_NAME).clear();
    });
  }
}

export default PeopleIDB;