import store from '@/store';
import { Module, VuexModule, Mutation, Action, getModule } from 'vuex-module-decorators';
import Person from '@/data/person';
import PeopleIDB from '@/databases/peopleidb';
import NewPerson from '@/data/newperson';
import { PersonData } from '@/interfaces/ipeople';

const peoplidb = new PeopleIDB('people3', 1);

// peoplidb.addPerson({
//   email: 'Richard@Richard.com',
//   name: 'Richard',
//   deleted: false,
//   color: 'blue',
//   image: 3
// });

export interface PeopleState {
  people: Array<Person>;
}

@Module({ dynamic: true, store, name: 'peopleModule' })
export default class PeopleModule extends VuexModule implements PeopleState {
  public people: Array<Person> = [];

  @Mutation
  addLocalPerson(p: Person): void {
    this.people = [...this.people, p];
  }

  @Mutation
  updateLocalPerson(p: Person): void {
    const localIndex = this.people.findIndex((person: Person) => person.email === p.email);
    this.people[localIndex] = p;
    this.people = [...this.people];
  }

  @Mutation
  updatePeople(people: PersonData[]): void {
    this.people = [];
    people.forEach((person: PersonData) => this.people.push(new Person(person)));
  }

  @Mutation
  deleteLocalPeople(people: PersonData[]): void {
    this.people = [];
  }

  @Action({commit: 'updatePeople'})
  async fetchPeople(): Promise<PersonData[]> {
    const people: PersonData[] = await peoplidb.getPeople();
    return people;
  }

  @Action({commit: 'addLocalPerson'})
  async addPerson(pd: PersonData): Promise<Person> {
    await peoplidb.addPerson(pd);
    return new Person(pd);
  }

  @Action({commit: 'updateLocalPerson'})
  async updatePerson(pd: PersonData): Promise<Person> {
    await peoplidb.addPerson(pd);
    return new Person(pd);
  }
  
  @Action({commit: 'deleteLocalPeople'})
  async deleteAllPeople(): Promise<boolean> {
    const result = await peoplidb.deleteAllPeople();
    return result;
  }

  get allPeople(): Array<Person> {
    return this.people.filter(person => !person.isSelf);
  }

  get personByEmail() {
    return (email: string): Person | undefined => 
      email === "new"
      ? new NewPerson()
      : this.people.find(person => person.email === email);
  }

  get exportPeople(): Promise<PersonData[]> {
    return peoplidb.getPeople();
  }

  get selfPerson(): Person | undefined {
    return this.people.find(person => person.isSelf);
  }
}

export const peopleModule = getModule(PeopleModule);