import Person from './person';

class NewPerson extends Person {

  constructor() {
    super({
      name: "New Contact",
      email: "new",
      deleted: false
    })
  }

  get isNewContact(): boolean {
    return true;
  }
  
  get initials(): string {
    return "+"
  }
  
  get color(): string {
    return "#66BB6A"
  }
}

export default NewPerson;