


















import Vue from 'vue';
import iconConfig from '@/store/misc/icons';
import { acModule } from '../store/modules/acs';
import { Bonus } from '../interfaces/generic';

export default Vue.extend({
  name: 'LoginBonus',
  data() {
    return {
      active: false,
      iconConfig
    }
  },
  methods: {
    applyBonus() {
      const localBonus = localStorage.getItem('bonus');
      const bonus: Bonus = localBonus ? JSON.parse(localBonus) : {
        points: 0,
        lastLogin: Date.now(),
        streak: 0,
        maxStreak: 0
      };
      const lastDate = new Date(bonus.lastLogin);
      const nowDate = new Date()
      if (bonus.lastLogin < Date.now() - 1000 * 60 * 60 * 24 || lastDate.getDay() !== nowDate.getDay()) {
        bonus.points++;
        bonus.lastLogin = Date.now();
        bonus.streak = bonus.lastLogin > Date.now() - 1000 * 60 * 60 * 24 * 2 && lastDate.getDay() === (nowDate.getDay() + 7 - 1) % 7
          ? bonus.streak + 1
          : 1;
        bonus.maxStreak = bonus.streak > bonus.maxStreak ? bonus.streak : bonus.maxStreak;
        localStorage.setItem('bonus', JSON.stringify(bonus));
        this.active = true;
        acModule.loginBonusUpdated();
      }
    }
  },
  computed: {    

  },
  mounted() {
    this.applyBonus();
  }
});
