import Vue from 'vue';
import Vuex from 'vuex';
import { ACState } from '@/store/modules/acs';
import { PeopleState } from '@/store/modules/people';
import { TopicState } from '@/store/modules/topics';
import { ImageState } from '@/store/modules/images';
import { ConfigState } from '@/store/modules/config';
import { ServiceWorkerState } from '@/store/modules/sw';

Vue.use(Vuex);

export interface RootState {
  topics: TopicState;
  people: PeopleState;
  acs: ACState;
  images: ImageState;
  config: ConfigState;
  sw: ServiceWorkerState;
}

const store = new Vuex.Store<RootState>({});

export default store;