















































































































































































































































































import Vue from 'vue';
import { acModule } from '@/store/modules/acs';
import iconConfig from '@/store/misc/icons';
import Person from '../data/person';
import { peopleModule } from '../store/modules/people';

export default Vue.extend({
  name: 'Home',
  data() {
    return {
      iconConfig
    }
  },
  computed: {
    sendCount(): number {
      return acModule.pendingACs.length;
    },
    receivedCount(): number {
      return acModule.receivedACs.length;
    },
    sentCount(): number {
      return acModule.sentACs.length;
    },
    selfPerson(): Person | undefined {
      return peopleModule.selfPerson;
    },
    welcomeMessage(): string {
      const hour: number = new Date().getHours();
      // const name: string = this.selfPerson ? this.selfPerson.name : ''
      if (hour > 17) return 'Good evening';
      if (hour > 11) return 'Good afternoon';
      if (hour > 5) return 'Good morning';
      return 'Good night';
    },
    userImageSource(): string {
      return (this.selfPerson && this.selfPerson.image) ? require(`@/assets/${this.selfPerson.image.path}`) : '';
    }
  }
  
});
