import store from '@/store';
import { Module, VuexModule, Mutation, Action, getModule } from 'vuex-module-decorators';
import imageJSON from '@/store/misc/images.json';


export interface UserImage {
  id: number;
  path: string;
}

export interface ImageState {
  images: Array<UserImage>;
}

@Module({ dynamic: true, store, name: 'imageModule'})
export default class ImageModule extends VuexModule implements ImageState {
  public images: Array<UserImage> = [];

  @Mutation
  updateImages(images: Array<UserImage>): void {
    this.images = images;
  }

  @Action({commit: 'updateImages'})
  async fetchImages(): Promise<Array<UserImage>> {
    return imageJSON.images as Array<UserImage>;
  }

  get imageById() {
    return (id: number): UserImage | undefined => {
      return this.images.find(image => image.id === id);
    }
  }

  get allImages(): Array<UserImage> {
    return this.images;
  }
}

export const imageModule = getModule(ImageModule);