import store from '@/store';
import { Module, VuexModule, Mutation, Action, getModule } from 'vuex-module-decorators';
import topicsSource from '@/store/misc/topics.json';
import { AppreciationTopic } from '@/interfaces/iac';

export interface TopicState {
  topics: Array<AppreciationTopic>;
}

@Module({ dynamic: true, store, name: 'topicModule'})
export default class TopicModule extends VuexModule implements TopicState {
  public topics: Array<AppreciationTopic> = [];

  @Mutation
  updateTopics(topics: Array<AppreciationTopic>): void {
    this.topics = topics;
  }

  @Action({commit: 'updateTopics'})
  async fetchTopics(): Promise<AppreciationTopic[]> {
    //TODO: fetch Topics somewhere
    return topicsSource.topics as AppreciationTopic[];
  }

  get topicById() {
    return (id: number): AppreciationTopic => 
      this.topics.find(topic => topic.id === id)!;
  }
  get allTopics(): Array<AppreciationTopic> {
    return this.topics;
  }
}

export const topicModule = getModule(TopicModule);