import store from '@/store';
import { Module, VuexModule, Mutation, Action, getModule } from 'vuex-module-decorators';
import { BeforeInstallPromptEvent } from '@/registerInstallEvent';


export interface ConfigState {
  installEvent: BeforeInstallPromptEvent | undefined;
}

@Module({ dynamic: true, store, name: 'configModule'})
export default class ConfigModule extends VuexModule implements ConfigState {
  public installEvent: BeforeInstallPromptEvent | undefined = undefined;
  public lastReport: string | null = window.localStorage.getItem('lastReport');

  @Mutation
  updateInstallEvent(e: BeforeInstallPromptEvent): void {
    this.installEvent = e;
  }
  
  @Action({commit: 'updateInstallEvent'})
  setInstallEvent(e: BeforeInstallPromptEvent): BeforeInstallPromptEvent {
    return e;
  }

  get getInstallEvent(): BeforeInstallPromptEvent | undefined {
    return this.installEvent;
  }

  
  @Mutation
  updateLastReport(now: string): void {
    window.localStorage.setItem('lastReport', now);
    this.lastReport = now;
  }
  
  @Action({commit: 'updateLastReport'})
  setLastReport(): string {
    return Date.now().toString();
  }

  get recentReport(): boolean {
    if (!this.lastReport) return false;
    return parseInt(this.lastReport) > Date.now() - 1000 * 60 * 60;
  }
  
}

export const configModule = getModule(ConfigModule);