import { PersonData, MinimizedPerson } from "@/interfaces/ipeople";
import { UserImage, imageModule } from '@/store/modules/images';

const DEFAULT_PERSON_COLOR = "#546E7A"

class Person {
  private _name: string;
  private _initials: string;
  private _color: string;
  private _email: string;
  private _deleted: boolean;
  private _isSelf: boolean;
  private _image?: UserImage;

  constructor(pd: PersonData) {
    this._name = pd.name;
    this._initials = pd.initials || pd.name.substring(0, 1).toUpperCase();
    this._color = pd.color || DEFAULT_PERSON_COLOR;
    this._email = pd.email;
    this._deleted = pd.deleted;
    this._isSelf = pd.isSelf ? pd.isSelf : false;
    this._image = pd.image ? imageModule.imageById(pd.image) : undefined;
  }

  public minimize(includeEmail: boolean): MinimizedPerson {
    const msg: MinimizedPerson = {
      n: this._name
    };
    if (this._initials !== this._name.substring(0, 1)) msg.i = this._initials;
    if (this._color !== DEFAULT_PERSON_COLOR) msg.c = this._color;
    if (this._image !== undefined) msg.p = this._image.id;
    if (includeEmail) msg.e = this._email;
    return msg;
  }

  get name(): string {
    return this._name;
  }
  get initials(): string {
    return this._initials;
  }
  get color(): string {
    return this._color;
  }
  get email(): string {
    return this._email;
  }
  get deleted(): boolean {
    return this._deleted;
  }
  get isNewContact(): boolean {
    return false;
  }
  get isSelf(): boolean {
    return this._isSelf;
  }
  get image(): UserImage | undefined {
    return this._image;
  }

  public isEqual(pd: PersonData): boolean {
    if (pd.initials && pd.initials !== this._initials) return false;
    if (pd.color && pd.color !== this._color) return false;
    if (pd.image && this._image && pd.image !== this._image.id) return false;
    return (
      pd.name === this._name
      && pd.email === this._email
      && pd.deleted === this._deleted
    );
  }
}

export default Person;