import { AppreciationCardData, APPRECIATION_CARD_STATE, AppreciationTopic, AppreciationSubTopic, MinimizedAppreciationCard, ConfirmationMessage, TOKEN } from '../interfaces/iac';
import Person from './person';

import { peopleModule } from '@/store/modules/people';
import { topicModule } from '@/store/modules/topics';

class AppreciationCard {
  private _id: number;
  private _sender: Person;
  private _receiver: Person;
  private _message: string;
  private _dateCreated: number;
  private _dateSent: number;
  private _state: APPRECIATION_CARD_STATE;
  private _topic!: AppreciationTopic;
  private _subtopics!: Array<AppreciationSubTopic>;
  private _token: TOKEN;
  private _bonus: number;
  private _streak: number;

  constructor(acd: AppreciationCardData) {
    this._id = acd.id ? acd.id : -1;
    this._sender = peopleModule.personByEmail(acd.sender)!;
    this._receiver = peopleModule.personByEmail(acd.receiver)!;
    this._message = acd.message;
    this._dateCreated = acd.dateCreated;
    this._dateSent = acd.dateSent;
    this._state = acd.state;

    this.handleTopic(acd);

    this._token = acd.token ? acd.token : TOKEN.NONE;
    this._bonus = acd.bonus ? acd.bonus : 0;
    this._streak = acd.streak ? acd.streak : -1;
  }

  private handleTopic(acd: AppreciationCardData): void {
    if (typeof acd.topic === 'number') {
      this._topic = topicModule.topicById(acd.topic);
      this._subtopics = this._topic.subtopics.filter((subtopic) => acd.subtopics.includes(subtopic.id));
      const customSubTopic: string | undefined = acd.subtopics.find(value => typeof value === 'string') as string | undefined;
      if (customSubTopic) this._subtopics.push({
        id: -1,
        text: customSubTopic,
        deleted: false
      } as AppreciationSubTopic)
    } else {
      this._topic = {
        id: -1,
        text: acd.topic,
        deleted: false,
        color: 'blue',
        icon: 'mdi-creation',
        subtopics: []
      } as AppreciationTopic;
      this._subtopics = [];
      acd.subtopics.forEach(subtopic => this._subtopics.push({
        id: -1,
        text: subtopic,
        deleted: false
      } as AppreciationSubTopic));
    }
  }

  public minimize(includePerson: boolean): MinimizedAppreciationCard {
    const msg: MinimizedAppreciationCard = {
      e: this._sender.email,
      m: this._message,
      d: this._dateCreated,
      t: this._topic.id >= 0 ? this._topic.id : this._topic.text,
      s: this._subtopics.map((subtopic: AppreciationSubTopic) => subtopic.id >= 0 ? subtopic.id : subtopic.text)
    };
    if (includePerson) msg.p = this._sender.minimize(false);
    return msg;
  }

  public toACData(): AppreciationCardData {
    const msg: AppreciationCardData = {
      id: this._id,
      sender: this._sender.email,
      receiver: this._receiver.email,
      message: this._message,
      dateCreated: this._dateCreated,
      dateSent: this._dateSent,
      state: this._state,
      topic: this._topic.id >= 0 ? this._topic.id : this._topic.text,
      subtopics: this._subtopics.map((subtopic: AppreciationSubTopic) => subtopic.id >= 0 ? subtopic.id : subtopic.text),
      token: this._token
    };
    return msg;
  }

  public confirm(includePerson: boolean): ConfirmationMessage {
    const msg: ConfirmationMessage = {
      e: this._receiver.email,
      d: this._dateCreated,
      n: this.dateSent
    }
    if (includePerson) msg.p = this._receiver.minimize(false);
    return msg;
  }

  // Assume that the sender can't make two different cards in the same millisecond
  public equals(mac: MinimizedAppreciationCard) {
    return (
      this._sender.email === mac.e
      && this._dateCreated === mac.d
    )
  }

  get id(): number {
    return this._id
  }
  get sender(): Person {
    return this._sender
  }
  get receiver(): Person {
    return this._receiver
  }
  get message(): string {
    return this._message
  }
  get dateCreated(): number {
    return this._dateCreated
  }
  get dateSent(): number {
    return this._dateSent
  }
  set dateSent(date: number) {
    this._dateSent = date;
  }
  get state(): APPRECIATION_CARD_STATE {
    return this._state
  }
  set state(s: APPRECIATION_CARD_STATE) {
    this._state = s;
  }
  get topic(): AppreciationTopic {
    return this._topic
  }
  get subtopics(): Array<AppreciationSubTopic> {
    return this._subtopics
  }
  get token(): TOKEN {
    return this._token;
  }
  get bonus(): number {
    return this._bonus;
  }
  get streak(): number {
    return this._streak;
  }
}

export default AppreciationCard;